let count = 20;
import goBack from '@/components/back.vue';
import simpleAddress from '@/components/simple-address/simple-address.nvue';
export default {
	data() {
		return {
			tabId:1,
			imgUrl:getApp().globalData.imgUrl,
			cityPickerValueDefault: [0, 0, 0],
			moneyCurrentPage:1,
			prizeCurrentPage:1,
			moneyNext:true,
			prizeNext:true,
			moneyList:'',
			prizeList:'',
			writeMsg:false,
			isDisabled:false,
			username:'',
			address:'',
			tel:'',
			yzcode:'',
			template:'',
			idcard:'',
			sec:0,
			stop:false,
			index:0,
			showTip:false,
			needIdcard:false,
			pickerText:'',
			safeAreaTop:getApp().globalData.safeAreaTop?getApp().globalData.safeAreaTop+'px':'64rpx'
		}
	},
	onLoad() {
		this.queryAllGiftsList();
	},
	methods: {
		tab(type){
			this.tabId=type;
			if(!this.moneyList){
				this.queryAllGiftsList();
			}else if(this.prizeCurrentPage==1&&!this.prizeList){
				this.queryPrizeList();
			}
		},
		openAddres() {
			if(this.isDisabled){
				return false;
			}
			this.$refs.simpleAddress.open();
		},
		onConfirm(e) {
			this.pickerText = e.label;
		},
		inputYzm(e){
			this.yzcode = e.detail.value;
		},
		inputName(e){
			this.username = e.detail.value;
		},
		inputTel(e){
			this.tel = e.detail.value;
		},
		inputAddress(e){
			this.address = e.detail.value;
		},
		inputIdcard(e){
			this.idcard = e.detail.value;
		},
		queryAllGiftsList() {
			uni.showLoading({
				title:'加载中'
			})
			uni.request({
				url: this.$Store.state.requestUrl + '/gifts/queryAllGiftsList',
				method: 'POST',
				data: {
					"openid": getApp().globalData.openid,
					// "searchFlag": 3,
					"currentPage": this.moneyCurrentPage,
					"count": count,
					"projectServerName":this.$Store.state.serverName
				}
			}).then(res=>{
				uni.hideLoading()
				let [e,r] = res;
				if(r){
					if(r.data.reply&&(!r.data.reply.objList||r.data.reply.objList.length<count)){
						this.moneyNext = false;
					}else{
						this.moneyNext = true;
					}
					if(this.moneyCurrentPage==1){
						this.moneyList = r.data.reply.objList;
					}else{
						if(r.data.reply.objList&&r.data.reply.objList.length>0){
							this.moneyList = this.moneyList.concat(r.data.reply.objList);
						}
					}
				}else{
					console.log(e);
					uni.showModal({
						title:'扫码记录提示',
						content:'系统繁忙',
						showCancel:false
					})
				}
			}).catch(err=>{
				uni.hideLoading();
				console.log(err)
			})
		},
		queryPrizeList(type) {//暂时不做数据缓存切换，每次直接调接口
			uni.request({
				url: this.$Store.state.requestUrl + '/gifts/queryPrizeList',
				method: 'POST',
				data: {
					"openid": getApp().globalData.openid,
					"currentPage": this.currentPage,
					"count": count,
					"projectServerName":this.$Store.state.serverName
				}
			}).then(res=>{
				let [e,r] = res;
				if(r){
					if(r.data.result.code==0){
						if(r.data.result.businessCode==0){
							if(this.prizeCurrentPage==1){
								this.prizeList = r.data.reply.prizeRecordAry;
							}else{
								if(r.data.reply.prizeRecordAry&&r.data.reply.prizeRecordAry.length>0){
									this.prizeList = this.prizeList.concat(r.data.reply.prizeRecordAry);
								}
							}
							if(!r.data.reply.prizeRecordAry||r.data.reply.prizeRecordAry.length<1){
								this.prizeNext = false;
							}else if(r.data.reply.prizeRecordAry.length<count){
								this.prizeNext = false;
							} else{
								this.prizeNext = true;
							}
						} else if(r.data.result.businessCode==1){
							uni.showModal({
								title:'提示',
								content:'你还未参与过扫码活动',
								showCancel:false
							})
							this.prizeNext = false;
						} else {
							uni.showModal({
								title:'提示',
								content:r.data.result.msg?r.data.result.msg:'系统开了个小差~',
								showCancel:false
							})
							this.prizeNext = false;
						}
					}else{
						uni.showModal({
							title:'大奖列表接口提示',
							content:r.data.result.msg?r.data.result.msg:'系统开了个小差~',
							showCancel:false
						})
						this.prizeNext = false;
					}
				}else{
					console.log(e);
					uni.showModal({
						title:'大奖列表接口提示',
						content:'系统繁忙',
						showCancel:false
					})
					this.prizeNext = false;
				}
			})
		},
		lower(){//上拉加载
			if(this.moneyNext&&this.tabId==1){
				this.moneyCurrentPage++;
				this.queryAllGiftsList()
			} else if(this.prizeNext&&this.tabId==2){
				this.prizeCurrentPage++;
				this.queryPrizeList();
			} else {
				uni.showToast({
					title:'没有更多了记录了',
					icon:'none'
				})
			}
			
		},
		openInput(index){
			this.writeMsg = true;
			this.index = index;
			if(this.prizeList[index].useStatus==1){//已领取
				this.isDisabled = true;
				this.tel = this.prizeList[index].phoneNum;
				this.username = this.prizeList[index].userName;
				this.address = this.prizeList[index].address.split('-')[3];
				this.pickerText = `${this.prizeList[index].address.split('-')[0]}-${this.prizeList[index].address.split('-')[1]}-${this.prizeList[index].address.split('-')[2]}`;
			}else{
				this.isDisabled = false;
			}
		},
		submsg() {
			if(this.isDisabled){
				this.writeMsg = false;
				return false;
			}
			if(this.username==''||this.username==' '){
				uni.showModal({
					title: "提示",
					content: '请填写正确的姓名哦！~',
					showCancel: false
				})
			}else if(this.needIdcard&&!this.$Store.dispatch(this.idcard)){
				uni.showModal({
					title: "提示",
					content: '请填写正确的身份证号码哦！~',
					showCancel: false
				})
			}else if(!(/^[1]{1}[0-9]{10}$/).test(this.tel)){
				uni.showModal({
					title: "提示",
					content: '请输入正确的手机号',
					showCancel: false
				})
			}  else if(this.pickerText==''){
				uni.showModal({
					title: "提示",
					content: '请选择所在地区',
					showCancel: false
				})
			} else if (this.address==''||this.address==' ') {
				uni.showModal({
					title: "提示",
					content: '请输入正确的详细地址',
					showCancel: false
				})
			} else if(!/^[0-9]{4}$/.test(this.yzcode)){
				uni.showModal({
					title: "提示",
					content: '请输入验证码',
					showCancel: false
				})
			} else {
				uni.showLoading({
					title:'提交中'
				})
				uni.setStorage({
					key:'address',
					data:getApp().globalData.address
				})
				uni.request({
					url: this.$Store.state.requestUrl + '/user/savePrize',
					method: 'POST',
					data: {
						"openid": getApp().globalData.openid,
						"address": this.pickerText+'-'+this.address,
						"username": this.username,
						"idcard": 'idcard',
						"skukey": this.prizeList[this.index].skukey,
						"phonenum": this.tel,
						"prizeInfoKey": this.prizeList[this.index].infoKey,
						"prizeVcode": this.prizeList[this.index].prizeVcode,
						"projectServerName":this.$Store.state.serverName,
						"captcha":this.yzcode
					}
				}).then(res=>{
					uni.hideLoading();
					let [e,r] =res;
					if(r){
						if(r.data.result.code==0){
							if(r.data.result.businessCode == 0){//提交成功
								this.showTip = true;
								this.yzcode = '';
								this.writeMsg = false;
								this.prizeList = '';
								this.prizeCurrentPage = 1;
								this.prizeNext = true;
								this.queryPrizeList();
							}else{
								uni.showModal({
									title: "提示",
									content: r.data.result.msg?r.data.result.msg:'系统开了个小差',
									showCancel: false
								})	
							}
						}else{
							uni.showModal({
								title: "大奖提交提示",
								content: r.data.result.msg?r.data.result.msg:'系统开了个小差',
								showCancel: false
							})	
						}
					}else{
						console.log(e)
						uni.showModal({
							title: "大奖提交提示",
							content: '系统繁忙',
							showCancel: false
						})
					}
				})
			}
		},
		timeout() {
			let timer = null;
			this.sec -= 1;
			if (this.sec <= 0 || this.stop) {
				return this.sec = 0;
			}
			timer = setTimeout(() => {
				this.timeout()
			}, 1000)
		},
		getVerifyCode(){
			if(this.sec!=0){
				return false;
			}else if (!(/^1[0-9]{10}$/.test(this.tel))) {
				uni.showModal({
					title: "提示",
					content: "请输入正确的手机号",
					showCancel: false
				})
			} else {
				uni.showLoading({
					title:'获取验证码',
					mask:true
				})
				uni.request({
					url:this.$Store.state.requestUrl+'/user/getCaptcha',
					method:'POST',
					data:{
						phonenum:this.tel,
						"projectServerName":this.$Store.state.serverName
					}
				}).then(res=>{
					uni.hideLoading()
					let [e,r] = res;
					if(r){
						if(r.data.result.code==0&&r.data.result.businessCode==0){//成功
							this.sec = 60;
							this.timeout();//开始倒计时
						}else if(r.data.result.code==0&&r.data.result.businessCode==2){
							uni.showModal({
								title: "提示",
								content: '您填写的手机号错误，发送验证码失败！',
								showCancel: false
							})
						}else{
							uni.showModal({
								title: "获取验证码提示",
								content:'验证码获取失败！请稍后再试并确认手机号是否输入正确',
								showCancel: false
							})
						}
					}else{
						uni.showModal({
							title: "获取验证码提示",
							content: '系统繁忙',
							showCancel: false
						})
					}
				})
			}
		}
	},
	components:{
		goBack,
		simpleAddress
	}
}
